<template>
  <b-container fluid id="app">
    <div class="row">
      <div class="col-sm-6 col-md-4 offset-md-4" v-if="logged === false">
        <h1 class="text-center login-title"><b>登入之後繼續</b></h1>
        <div class="account-wall">
          <img class="profile-img" src="https://lh5.googleusercontent.com/-b0-k99FZlyE/AAAAAAAAAAI/AAAAAAAAAAA/eu7opA4byxI/photo.jpg?sz=120"
               alt="">
          <form class="form-signin" v-on:submit.prevent="login">
            <input type="text" class="form-control" placeholder="Email" v-model="inputAccount" required autofocus>
            <input type="password" class="form-control" placeholder="Password" v-model="inputPassword" required>
            <button class="btn btn-lg btn-outline-info btn-block" type="submit">
              登入</button>
          </form>
        </div>
      </div>
    </div>
    <b-card no-body>
      <b-tabs pills card vertical nav-wrapper-class="w-50">
        <b-tab title="Tab 1" active>
          <ul>
            <li v-for="(data, index) in item" style="margin-bottom: 15px">
              <b-list-group>
                <b-list-group-item href="#" active class="flex-column align-items-start">
                  <div class="d-flex w-100 justify-content-between">
                    <h5 class="mb-1">{{data.title}}</h5>
                  </div>
                  <p class="mb-1">
                    {{data.text}}
                  </p>
                  <small>id: {{index}}</small>
                  <br>
                    <b-button class="btn-outline-danger">刪除</b-button>
                    <b-button class="btn-outline-warning">保護</b-button>
                    <b-button class="btn-outline-info">編輯</b-button>
                </b-list-group-item>
              </b-list-group>
            </li>
          </ul>
        </b-tab>
        <b-tab title="Tab 2">
          <b-form>
            <b-form-input class="mb-5" placeholder="請輸入標題" v-model="inputTitle" required></b-form-input>
            <b-form-input class="mb-5" placeholder="請輸入圖片網址" v-model="inputInnerPic" required></b-form-input>
            <b-form-input class="mb-5" placeholder="請輸入內文" v-model="inputInnerText" required></b-form-input>
            <b-form-input class="mb-5" placeholder="請輸入標籤" v-model="inputHashTag"></b-form-input>
          </b-form>
        </b-tab>
        <b-tab title="Tab 3">
          Tab Contents 3
        </b-tab>
      </b-tabs>
    </b-card>
    <div class="row" v-if="logged === true">
    </div>
  </b-container>
</template>

<script>
  const storageRef = firebase.storage().ref('/images/')
  const database = firebase.database().ref().child('/content/')
  export default {
    name: "back",
    data () {
      return {
        inputAccount: '',
        inputPassword: '',
        logged: false,
        inputTitle: '',
        inputInnerText: '',
        inputInnerPic: '',
        inputHashTagArray: [],
        inputHashTagText: '',
        item: []
      }
    },
    methods: {
      login () {
        firebase.auth().signInWithEmailAndPassword(this.inputAccount, this.inputPassword)
          .then(() => {
            this.logged = true
          })
          .catch(error => {
            let errorcode = error.code
            console.log(errorcode)
          })
      },
      addHashTag () {
        this.inputHashTagArray.push(this.inputHashTagText)
      },
      newPost () {
        database.push({title: this.inputTitle, text: this.inputInnerText, pic: this.inputInnerPic, hashTag: this.inputHashTag})
      }
    },
    mounted () {
      let vm = this
      firebase.auth().onAuthStateChanged(function(user) {
        if (user) {
          database.on('value', (snap) => {
            vm.item = snap.val()
          })
          vm.logged = true
        } else {
          vm.logged = false
          // No user is signed in.
        }
      });
    }
  }
</script>

<style scoped>
  .form-signin
  {
    max-width: 330px;
    padding: 15px;
    margin: 0 auto;
  }
  .form-signin .form-signin-heading, .form-signin .checkbox
  {
    margin-bottom: 10px;
  }
  .form-signin .checkbox
  {
    font-weight: normal;
  }
  .form-signin .form-control
  {
    position: relative;
    font-size: 16px;
    height: auto;
    padding: 10px;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
  }
  .form-signin .form-control:focus
  {
    z-index: 2;
  }
  .form-signin input[type="text"]
  {
    margin-bottom: -1px;
    border-bottom-left-radius: 0;
    border-bottom-right-radius: 0;
  }
  .form-signin input[type="password"]
  {
    margin-bottom: 10px;
    border-top-left-radius: 0;
    border-top-right-radius: 0;
  }
  .account-wall
  {
    margin-top: 20px;
    padding: 40px 0px 20px 0px;
    background-color: #f7f7f7;
    -moz-box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.3);
    -webkit-box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.3);
    box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.3);
  }
  .login-title
  {
    font-size: 1.5rem;
    color: #555;
    margin-top: 100px;
    font-weight: 400;
    display: block;
    font-family: 'Noto Sans TC', 'Bungee Hairline', sans-serif;
  }
  .profile-img
  {
    width: 96px;
    height: 96px;
    margin: 0 auto 10px;
    display: block;
    -moz-border-radius: 50%;
    -webkit-border-radius: 50%;
    border-radius: 50%;
  }
</style>



